
import { create } from 'zustand';
import { 
  UserProfile, 
  WorkoutSession, 
  NutritionEntry, 
  SleepEntry, 
  ProgressEntry,
  TrainingPhase,
  WorkoutDay
} from '../types';
import { 
  authAPI, 
  userAPI, 
  workoutsAPI, 
  nutritionAPI, 
  sleepAPI, 
  progressAPI,
  trainingPhasesAPI
} from '../services/apiService';

interface AppStore {
  userProfile: UserProfile | null;
  currentPhase: TrainingPhase | null;
  workoutHistory: WorkoutSession[];
  workoutDays: WorkoutDay[];
  nutritionLog: NutritionEntry[];
  sleepLog: SleepEntry[];
  progressHistory: ProgressEntry[];
  
  // Actions
  setUserProfile: (profile: UserProfile) => Promise<void>;
  updateUserProfile: (updates: Partial<UserProfile>) => Promise<void>;
  logout: () => Promise<void>;
  setCurrentPhase: (phase: TrainingPhase) => Promise<void>;
  addWorkout: (workout: WorkoutSession) => Promise<void>;
  addWorkoutDay: (workout: WorkoutDay) => Promise<void>;
  addNutritionEntry: (entry: NutritionEntry) => Promise<void>;
  addSleepEntry: (entry: SleepEntry) => Promise<void>;
  addProgressEntry: (entry: ProgressEntry) => Promise<void>;
  loadUserData: () => Promise<void>;
}

export const useAppStore = create<AppStore>()((set, get) => ({
  userProfile: null,
  currentPhase: null,
  workoutHistory: [],
  workoutDays: [],
  nutritionLog: [],
  sleepLog: [],
  progressHistory: [],
  
  setUserProfile: async (profile: UserProfile) => {
    try {
      // Check if this is a new profile (no ID) or existing profile
      if (!profile.id) {
        // This is a new profile, create it
        const createdProfile = await userAPI.createProfile({
          name: profile.name,
          age: profile.age,
          height: profile.height,
          weight: profile.weight,
          bodyFatPercentage: profile.bodyFatPercentage,
          targetPhysique: profile.targetPhysique,
          equipment: profile.equipment,
          activityLevel: profile.activityLevel,
          sleepSchedule: profile.sleepSchedule,
          createdAt: profile.createdAt,
          updatedAt: profile.updatedAt
        });
        set({ userProfile: createdProfile });
      } else if (profile.id.startsWith('profile_')) {
        // This is a temporary profile ID, set it locally without database creation
        set({ userProfile: profile });
      } else {
        // This is an existing profile, update it
        await userAPI.updateProfile(profile);
        set({ userProfile: profile });
      }
    } catch (error) {
      console.error('Failed to save profile to database:', error);
      // Still set the profile locally so the user can continue
      set({ userProfile: profile });
    }
  },
  
  updateUserProfile: async (updates: Partial<UserProfile>) => {
    try {
      const currentProfile = get().userProfile;
      if (currentProfile) {
        const updatedProfile = { ...currentProfile, ...updates, updatedAt: new Date() };
        await userAPI.updateProfile(updatedProfile);
        set({ userProfile: updatedProfile });
      }
    } catch (error) {
      console.error('Failed to update profile in database:', error);
      set((state) => ({
        userProfile: state.userProfile ? { ...state.userProfile, ...updates, updatedAt: new Date() } : null
      }));
    }
  },
  
  logout: async () => {
    try {
      await authAPI.logout();
    } catch (error) {
      console.error('Failed to logout from database:', error);
    }
    
    set({
      userProfile: null,
      currentPhase: null,
      workoutHistory: [],
      workoutDays: [],
      nutritionLog: [],
      sleepLog: [],
      progressHistory: []
    });
  },
  
  setCurrentPhase: async (phase: TrainingPhase) => {
    try {
      await trainingPhasesAPI.update(phase.id, phase);
      set({ currentPhase: phase });
    } catch (error) {
      console.error('Failed to save phase to database:', error);
      set({ currentPhase: phase });
    }
  },
  
  addWorkout: async (workout: WorkoutSession) => {
    try {
      await workoutsAPI.create(workout);
      set((state) => ({
        workoutHistory: [...state.workoutHistory, workout]
      }));
    } catch (error) {
      console.error('Failed to save workout to database:', error);
      set((state) => ({
        workoutHistory: [...state.workoutHistory, workout]
      }));
    }
  },
  
  addWorkoutDay: async (workout: WorkoutDay) => {
    try {
      set((state) => ({
        workoutDays: [...state.workoutDays, workout]
      }));
    } catch (error) {
      console.error('Failed to save workout day:', error);
      set((state) => ({
        workoutDays: [...state.workoutDays, workout]
      }));
    }
  },
  
  addNutritionEntry: async (entry: NutritionEntry) => {
    try {
      await nutritionAPI.create(entry);
      set((state) => ({
        nutritionLog: [...state.nutritionLog, entry]
      }));
    } catch (error) {
      console.error('Failed to save nutrition entry to database:', error);
      set((state) => ({
        nutritionLog: [...state.nutritionLog, entry]
      }));
    }
  },
  
  addSleepEntry: async (entry: SleepEntry) => {
    try {
      await sleepAPI.create(entry);
      set((state) => ({
        sleepLog: [...state.sleepLog, entry]
      }));
    } catch (error) {
      console.error('Failed to save sleep entry to database:', error);
      set((state) => ({
        sleepLog: [...state.sleepLog, entry]
      }));
    }
  },
  
  addProgressEntry: async (entry: ProgressEntry) => {
    try {
      await progressAPI.create(entry);
      set((state) => ({
        progressHistory: [...state.progressHistory, entry]
      }));
    } catch (error) {
      console.error('Failed to save progress entry to database:', error);
      set((state) => ({
        progressHistory: [...state.progressHistory, entry]
      }));
    }
  },
  
  loadUserData: async () => {
    try {
      const [profile, workouts, nutrition, sleep, progress] = await Promise.all([
        userAPI.getProfile().catch(() => null),
        workoutsAPI.getAll().catch(() => []),
        nutritionAPI.getAll().catch(() => []),
        sleepAPI.getAll().catch(() => []),
        progressAPI.getAll().catch(() => [])
      ]);
      
      if (profile) {
        set({ userProfile: profile });
      }
      
      // Separate workouts by type
      const workoutSessions: WorkoutSession[] = [];
      const workoutDays: WorkoutDay[] = [];
      
      workouts.forEach((workout: any) => {
        if ('trainingDay' in workout) {
          workoutSessions.push(workout as WorkoutSession);
        } else {
          workoutDays.push(workout as WorkoutDay);
        }
      });
      
      set({
        workoutHistory: workoutSessions,
        workoutDays,
        nutritionLog: nutrition,
        sleepLog: sleep,
        progressHistory: progress
      });
    } catch (error) {
      console.error('Failed to load user data:', error);
    }
  },
}));

